@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@367&display=swap);
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

li {
    list-style: none;
}

.scroll-to-top {
    position: fixed;
    bottom: 1.7rem;
    right: 1.7rem;
    -webkit-animation: fadeIn 700ms ease-in-out 1s both;
            animation: fadeIn 700ms ease-in-out 1s both;
    cursor: pointer;
    color: #ccc;
    background-color: transparent;
    border: 1px solid #ccc;
    z-index: 99;
    overflow: hidden;
}

.scroll-tt {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
}

.scroll-tt svg {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}

@-webkit-keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.teee {
    border-bottom: 1px solid #1d1d1d;
}

.footer {
    background-color: #f4f2f2;
}

.footer__wrapper {
    display: flex;
    height: 400px;
    flex-wrap: wrap;
    padding-top: 90px;
    justify-content: space-around;
}

.footer__wrapper h4 {
    font-family: josefin sans, sans-serif;
    font-weight: 600;
    text-transform: uppercase;
    color: #1d1d1d;
    font-size: 16px;
    line-height: 1.5em;
    letter-spacing: .12em;
    margin: 25px 0;
    -ms-word-wrap: break-word;
    word-wrap: break-word;
}

.footer__wrapper p {
    color: #8b8b8b;
    font-family: sans-serif;
    font-size: 16px;
    line-height: 33px;
}

/* RESPOSIVE------------- */
@media only screen and (max-width: 768px) {
    .footer__wrapper-contact {
        margin: 0 80px;
        background-color: #f4f2f2;
    }

    .footer__wrapper-help {
        margin-right: 180px;
    }
}

@media only screen and (max-width: 600px) {
    .footer__wrapper-help {
        margin-right: 100px;
    }
}

@media only screen and (max-width: 414px) {
    .footer__wrapper-help {
        margin-left: 40px;
    }

    .footer__wrapper-about {
        margin-left: 45px;
        margin-right: 80px;
    }
}

.header {
    display: flex;
    padding: 20px 50px;
    position: relative;
    transition: .8s cubic-bezier(.77, 0, .175, 1);
    background-color: #ffffff;
    z-index: 9;
}

#header-link {
    opacity: 0;
    -webkit-animation: captionText .5s ease forwards;
            animation: captionText .5s ease forwards;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}

@-webkit-keyframes captionText {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-100px);
                transform: translateX(-100px);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}

@keyframes captionText {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-100px);
                transform: translateX(-100px);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}

/* Custom cart */
.header__wrapper-right .count-cart span {
    font-family: "Josefin Sans", sans-serif;
    font-weight: 600;
    text-transform: uppercase;
    color: #1d1d1d;
    font-size: 13px;
    -webkit-transform: translateX(-48px) !important;
            transform: translateX(-48px) !important;
    font-weight: 400;
    letter-spacing: .25em;
    margin-top: 8px;
}

.header__wrapper-right .count-cart {
    display: flex;
}

/* end Custom cart */

/* HEADER SHOW SCROLL */
.activenav {
    position: fixed;
    display: flex;
    width: 100%;
    -webkit-animation: slide-down .7s ease forwards;
            animation: slide-down .7s ease forwards;
    box-shadow: 0px 13px 25px -12px rgb(0 0 0 / 25%);
    z-index: 999;
    top: 0px;
    left: 0;
    font-size: 11px;
    align-items: center;
    padding: 13px 100px;
}

.activenav .lgg {
    width: 130px;
}

.activenav .header-cart-tab {
    display: none;
}

.activenav .header__wrapper-left a {
    font-size: 11px;
}

.activenav .header__wrapper-right {
    margin-top: 7px;
    margin-right: 40px;
}

@-webkit-keyframes slide-down {
    0% {
        opacity: 0;
        margin-right: 100px;
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }
    100% {
        opacity: 1;
        margin-right: 0;
        transition: .4s linear;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@keyframes slide-down {
    0% {
        opacity: 0;
        margin-right: 100px;
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }
    100% {
        opacity: 1;
        margin-right: 0;
        transition: .4s linear;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

/* END HEADER SHOW SCROLL */
