    justify-content: center;
    align-items: center;
    margin: 8px;
    background-color: transparent;
    width: 34px;
    border: .5px solid #9D9D9C;
}


.spin::before,
.spin::after {
    top: 0;
    left: 0;
}

.spin::before {
    border: 1px solid transparent;
}

.spin:hover > svg {
    color: #1d1d1d;
}


